.module-workloads {
	margin: -16px;
	background: $app-white;

	.module-workloads-header {
		border-bottom: 1px solid $border-dark-color;
		height: 56px;
		@include flexbox-simple(center, center);
		background: $white;
		top: 65px;
		left: 0;
		width: 100%;
		z-index: 4;

		.module-workloads-header-right {
			margin-left: auto;
		}

		.page-action_list {
			@include flexbox-simple(space-between, center);
		}

		.wlm-header-date {
			font-size: $title-small;
			font-weight: $text-weight-semi-bold;
			margin-left: 10px;
			width: 205px;
		}

		.wlm-capacity-option {
			width: 100%;
		}

		.change-summary {
			width: 150px;
			margin-left: 25px;
		}

		.entity-switcher-dropdown {
			min-width: 165px;
		}

		.effort-switcher-dropdown {
			min-width: 192px;
		}

		.btn-align {
			@include flexbox-simple(space-between, center);
		}

		.allocated-btn {
			@extend .btn-align;
			width: 192px;
		}

		.basic-dropdown-item:hover {
			background-color: $dropdown-hover  !important;
		}

		.basic-dropdown-item.active {
			font-weight: $text-weight-semi-bold;
			background-color: $dropdown-option-selected  !important;
			position: relative;
		}

		.wlm-legend-dropdown {
			min-width: 280px;

			.basic-dropdown-item {
				font-weight: $text-weight-normal;
				font-size: $text-normal;
				cursor: default;

				&:hover {
					background-color: transparent !important;
				}
			}



			.wlm-legend-group-label {
				font-weight: $text-weight-semi-bold;
				font-size: $title-small;
				display: flex;
				flex-direction: row;

				&::after {
					content: ' ';
					flex: 1 1;
					margin: auto;
					margin-left: 4px;
					border-bottom: 1px solid $border-light-color;

				}
			}

			.wlm-legend-label {
				display: inline-block;
				margin-left: 12px;
			}

			.wlm-legend-indicator {
				width: 24px;
				height: 24px;
				display: inline-block;
				vertical-align: middle;
			}

			.wlm-legend-type {
				&--under {
					background: $app-casablanca-light;
					border: 1px solid $alertPartialBorder;
				}

				&--over {
					background: $alertWarningBackground;
					border: 1px solid $alertWarningBorder;
				}

				&--optimally {
					background: $optimally-allocated-background;
					border: 1px solid $optimally-allocated-border;
				}

				&--not {
					background: $app-white;
					border: 1px dashed $tableRowHighlightMarker;
				}
			}

			.wlm-cell-legend {
				border-radius: 4px;
			}

			.wlm-legend-threshold {
				color: $secondary-text-color-2;
			}

			.wlm-gantt-legend {
				border-radius: 50%;

				&--open {
					background: $app-sidebar-bg;
					border: 1px solid $app-grey-bg-2;
				}

				&--in_progress {
					background: $alertInfoBackground;
					border: 1px solid $border-light-blue;
				}

				&--done {
					background: $app-jade-light;
					border: 1px solid $hummingBirdGreenBorder;
				}

				&--delayed {
					background: $alertWarningBackground;
					border: 1px solid $alertWarningBorder;
				}
			}
		}
	}


	.basic-dropdown-item {
		&.active {
			font-weight: $text-weight-semi-bold  !important;
		}

		font-weight: $text-weight-medium;
		padding : 0;
		text-align : left;
	}
}


.workload-entity-dropdown {
	right: 8px;
	width: 225px !important;
	overflow: hidden;
}

.ui-component-check-box {
	label {
		cursor: pointer;
	}
}

.module-workload-entity {
	margin: 8px 0 12px 0;

	&-header {
		padding: 20px 0px 8px 18px;
	}

	&.nested {
		padding-left: 20%;
	}
}

.wlm-entity-dropdown {
	&--active {
		@include filter-active();
	}
}

.entity-filter-footer {
	@include flexbox-simple(flex-end, center);
	padding: 12px;
}

.workload-shimmer-container {
	.module-workloads-header {
		left: 60px;
		width: 100%;
		z-index: 10;
	}
}
