$slider-width: 675px;

.manage-unassignment-slider {
	.ember-modal-header {
		padding: 20px 12px 12px 24px !important;
	}

	.ember-modal-body {
		border-top: 1px solid $border-light-color;
	}

	.highlight-assign {
		border: 1px solid $linkColor  !important;

		path {
			fill: $linkColor  !important;
		}
	}
}

.manage-assignment-slider,
.manage-unassignment-slider {
	width: $slider-width  !important;

	.wlm-sort-icon {
		padding: 4px 5px;
	}

	.edit-assignments-loader-wrapper {
		margin: 20px;
	}

	.wlm-manage-assignment-card-value {
		margin-left: 5px;
	}

	.ember-modal-header {
		padding: 20px 12px 12px 24px !important;
		border-top: none;
	}

	.ember-modal-footer {
		width: $slider-width  !important;
		z-index: 2;
	}

	.ml-5 {
		margin-left: 5px;
	}

	.day-level-details {
		margin: 0 !important;
	}

	.day-level-effort,
	.day-level-status {
		margin-top: 4px;
	}

	.date-banner-title {
		color: $app-form-label-color;

		.edit-assignments-date-banner {
			text-transform: none;
			font-weight: $text-weight-medium;
		}

		&::before {
			width: 6px;
			content: " ";
			position: absolute;
			border-left: 1px solid $app-grey-bg-2;
			height: 32px;
			top: 68px;
		}
	}

	.edit-assignments-date-banner {
		padding: 12px 0 0 6px;

		&.assignee-timezone-date {
			padding-top: 4px;
			color: $secondary-text-color-2;
		}
	}

	.edit-form-field--label,
	.form-field--label {
		display: block;
		color: $app-form-label-color;
		font-size: $text-normal;
		font-weight: $text-weight-semi-bold;
		word-break: break-word;
	}

	.plan-date-time-section {
		position: relative;
	}

	&.hide-footer {
		.ember-modal-body {
			position: absolute;
			padding: 0;
			width: 100%;
		}
	}

	.unassigned-title {
		font-weight: $text-weight-semi-bold;
		font-size: $title-normal;
		margin-bottom: 24px;
		line-height: 20px;
	}

	.agent-workload-overview {
		display: flex;


		.agent-workload-overview-img {
			margin-right: 12px;
			width: 40px;
			height: 40px;

			.shimmer-img--large,
			.avatar__initial,
			.avatar__icon {
				width: 40px;
				height: 40px;
			}
		}



		.agent-workload-overview-details {
			width: 100%;

			&-loader {
				width: 80%
			}

			.workload-header-banner {
				@include flexbox-simple(space-between, center);
				width: 95%;
			}
		}

		.agent-workload-overview-details-who,
		.agent-workload-overview-details-meta {
			color: $alertCancelColor;
			margin-bottom: 4px;
		}

		.agent-workload-overview-details-meta {
			text-transform: lowercase;
		}

		.ember-svg {
			vertical-align: middle;
		}

		.agent-workload-overview-details-hint {
			font-weight: $text-weight-semi-bold;
			font-size: $title-normal;
			margin-bottom: 6px;
		}
	}

	&-workload-type {
		float: right;
		font-weight: $text-weight-semi-bold;
		color: $primary-text-color-1;
	}

	&-end {
		float: right;
		color: $primary-text-color-1;
	}

	&-start,
	&-status {
		color: $primary-text-color-1;
	}

	&-date {
		font-weight: $text-weight-semi-bold;
		font-size: $title-small;
		margin-bottom: 4px;

		.ember-svg {
			vertical-align: middle;
		}
	}

	&-meta {
		.delimeter {
			padding: 0 4px;
		}
	}

	.agent-workload-overview-details-hint {
		font-weight: $text-weight-semi-bold;
		font-size: $title-small;
		margin-bottom: 4px;
	}

	.day-level-conflict-info {
		border: 1px solid $alertPartialBorder;
		background: $app-casablanca-lighter;
		width: 26px;
		height: 26px;
		border-radius: 4px;
		padding: 5px;
		margin-left: 6px;
		position: relative;
		cursor: pointer;

		.conflict-bubble {
			position: absolute;
			font-weight: $text-weight-semi-bold;
			font-size: $text-normal;
			line-height: 18px;
			background: $app-error-state;
			padding: 0 5px;
			border-radius: 9px;
			color: $app-white;
			bottom: 10px;
			left: 14px;
		}
	}

	.agent-workload-overview--details {

		&-title {
			color: $alertCancelColor;
			margin-bottom: 4px;
		}

		&-item {
			@include flexbox-simple(start, center);

			&-count {
				font-weight: $text-weight-medium;
				color: $btnPrimaryActiveBackground;

			}

			.access-conflict {
				margin: -6px 0 0;

				path {
					fill: $app-error-state;
				}
			}
		}

		&-date {
			font-weight: $text-weight-semi-bold;
			margin-bottom: 8px;
			font-size: $title-normal;
		}



	}

	.page-action-list {
		padding: 12px 12px 12px 24px;
		position: fixed;
		background: $app-white;
		width: $slider-width;
		border-top: 1px solid $border-light-color;
		z-index: 1;

		&-day {
			background: none;
			height: 2px;
		}


		.search-box {
			flex-grow: 4;
			margin-right: 8px;

			input {
				width: 100%;
			}
		}

		.dd-arrow {
			vertical-align: text-top;
		}
	}

	.unassigned-header {
		@include flexbox-simple(center, center);
		background: $app-white;

		.unassigned-entity-switcher {
			@include flexbox-simple(start, center);
			padding-right: 10px;
		}


		.search-box {
			flex-grow: 4;
			margin-right: 8px;

			input {
				width: 100%;
			}
		}

		.dd-arrow {
			vertical-align: middle;
			height: 15px;
		}
	}

	.agent-assginment--list {
		margin-top: 56px;
		width: $slider-width;

		.highlight-assign {
			border: 1px solid $linkColor  !important;

			path {
				fill: $linkColor;
			}
		}
	}

	.agent-assignment-list-day {
		margin-top: 0;

		.assignment-card--overview-action {
			display: flex;
			position: absolute;
			bottom: 30px;
		}
	}

	.wlm-sort-dropdown {
		.ember-basic-dropdown-content {
			min-width: 200px;
		}

		hr {
			margin: 8px -8px;
		}
	}

	.manage-assignment-filter-dropdown {

		&-day {
			top: -60px;
			left: -20px;
		}

		.ember-basic-dropdown-content {
			min-width: 330px;
			padding: 12px 16px;
		}

		.header-title {
			font-weight: $text-weight-semi-bold;
			font-size: $title-small;
			margin-top: 4px;
			margin-bottom: 12px;
		}

		.filter-section {
			border-top: 1px solid $border-light-color;
			padding: 8px 0;

			&--title {
				font-weight: $text-weight-semi-bold;
				font-size: $text-normal;
				margin-bottom: 8px;
			}

			&--controls {
				@include flexbox-simple(start, center);

				.assingment-dropdown-list {
					margin-right: 20px;
				}

				input {
					vertical-align: middle;
				}

				label {
					display: inline-block;
				}
			}
		}

		.filter-actions {
			float: right;
			margin-top: 16px;
			margin-right: -8px;
		}
	}

	.wlm-status-filter-dd,
	.wlm-group-filter-dd,
	.wlm-priority-filter-dd {
		.ember-basic-dropdown-content {
			padding: 0 !important;
		}
	}

	.manage-unassigned-items-filter-dropdown {
		.ember-basic-dropdown-content {
			padding: 12px 16px;
		}

		.ember-basic-dropdown-content:not(.ember-power-select-dropdown) {
			min-width: 288px;
		}

		.header-title {
			font-weight: $text-weight-semi-bold;
			font-size: $title-small;
			margin-top: 4px;
			margin-bottom: 12px;
		}

		.filter-section {
			border-top: 1px solid $border-light-color;
			padding: 8px 0;

			&--title {
				font-weight: $text-weight-semi-bold;
				font-size: $text-normal;
				margin-bottom: 8px;
			}

			&--controls {
				@include flexbox-simple(space-between, center);

				input {
					vertical-align: middle;
				}

				label {
					display: inline-block;
				}
			}
		}

		.filter-actions {
			float: right;
			margin-top: 16px;
			margin-right: -8px;
		}
	}

	.wlm-sort--icon {
		padding: 3px;
	}

	.assignment-card {
		display: flex;
		position: relative;
		padding: 12px 12px 12px 24px;
		border-bottom: 1px solid $border-light-color;

		&.highlight {
			background: $background-color-2;
		}

		&--icon {
			margin-right: 6px;
			margin-top: 2px;
		}

		.day-overview {
			display: grid;
			grid-template-columns: 50% 50%;
			grid-gap: 20px;
		}

		&--overview {
			width: 83%;

			&-title {
				font-size: $title-small;
				line-height: 20px;
				margin-bottom: 6px;
			}

			&-attr {
				color: $secondary-text-color-1;
				font-size: $text-normal;
				line-height: 20px;

				.overview-plan {
					margin: 0 0 4px;
				}

				.delimeter {
					padding: 0 4px;
				}
			}

			&-action {
				margin-left: auto;
				position: absolute;
				height: 100%;
				right: 12px;
				top: 0px;
				@include flexbox-simple(center, center);
				display: none;

				.wlm-agent-edit {

					&:disabled {
						cursor: no-drop;
					}

					.agent-group-wrapper {
						position: absolute;
						top: 1px;
						left: 4px;
					}

					&-active {
						border: 1px solid $linkColor  !important;
					}
				}

				.agent-group-trigger {
					height: 28px !important;
					opacity: 0;
					z-index: 0;
					position: absolute;
					left: -4px;
					width: 30px;
				}

				.assign-to-tooltip {
					visibility: hidden !important;
				}

				.add-user {
					border: none !important;
				}

				.edit-btn {
					padding: 0;
					min-width: 32px;
					position: relative;
					@include flexbox-simple(center, center);
				}
			}
		}

		&:hover {
			background: $background-color-2;

			.assignment-card--overview-action {
				display: flex;
			}
		}
	}

	.unassigned-assignment-card {
		.assignment-card--overview {
			.assignment-card--overview-title {
				margin-bottom: 2px;
				margin-top: 4px;
			}
		}

		.assignment-card--overview-action {
			.ember-svg {
				path {
					fill: $app-icon-color;
				}
			}
		}

		&:hover {
			background: $background-color-2;

			.assignment-card--overview-action {
				display: flex;
			}
		}

		.assignment-card--icon {
			margin-top: 6px;
		}
	}

	.assignment-card-loader {
		padding: 12px 12px 12px 24px;
		border-bottom: 1px solid $border-light-color;

		.shimmer-loader {
			z-index: -1;
		}

		.shimmer-loader-item {
			margin: 4px;
		}

		&:hover {
			background: transparent;
		}
	}

	.wlm-manage-assignment-empty {
		@include flexbox-simple(center, center);
		flex-wrap: wrap;
		flex-direction: column;
		height: calc(100vh - 180px);

		.message {
			font-weight: $text-weight-medium;
			line-height: 20px;
			padding: 8px;
		}
	}

	.wlm-edit-assignments-container {
		padding: 0 28px;

		.inline-messages {
			margin: 18px 0;
		}

		.parent-module-card {
			box-shadow: 0 0 1px 1px $checkboxHoverShadow;
			margin-bottom: 32px;
			position: relative;
			padding: 12px;

			&::after {
				content: " ";
				border-bottom: 1px solid $modal-footer-border-color;
				position: absolute;
				top: 60px;
				left: 0;
				width: 100%
			}

			.sub-detail {
				position: relative;
			}
		}

		.parent-module-icon {
			width: 28px;
			display: inline-block;
			position: absolute;
			height: 28px;
			top: -4px;
			background: $section-background-blue;
			border: 1px solid $border-light-blue;
			border-radius: 4px;
			@include flexbox-simple(center, center);

			.ember-svg {
				fill: $linkColor
			}
		}

		.parent-module-info {
			font-weight: $text-weight-medium;
			color: $app-grey;

			&--id {
				margin-left: 40px;
				padding-right: 5px;
			}

			&--title {
				color: $linkColor;
			}
		}

		.wlm-edit-assignments {
			&-heading {
				font-weight: $text-weight-semi-bold;
				display: inline-flex;
				margin-bottom: 10px;

				&--no-link {
					color: $app-grey;
				}
			}

			&-title {
				color: $app-grey;
				font-size: $title-normal;
				font-weight: $text-weight-semi-bold;
				margin-bottom: 20px;
			}

			&-offset {
				@include line-clamp;
				float: right;
				width: 215px;
				padding-left: 5px;
			}
		}

		.ember-form {
			margin-top: 15px;
		}

		.edit-assignments-plan-section {
			font-size: $title-normal;
			font-weight: $text-weight-semi-bold;
			text-transform: uppercase;
		}

		.assignee-suggest-container {
			@include flexbox-simple(start, start);
			flex-direction: column;

			.form-field--label {
				display: none;
			}

			.project-suggest-label {
				padding-bottom: 4px;
				color: $app-form-label-color;
				font-size: 12px;
				font-weight: 600;
				line-height: 20px;
				word-break: break-word;
			}

			.ember-basic-dropdown {
				width: 300px !important;
			}
		}

		.date-time-split-field-wrapper .ember-basic-dropdown {
			position: unset;
		}

		.assigments-form-plan {
			.text-field {
				order: 2;
			}
		}

		.edit-assignments-section-header {
			margin-bottom: 20px;
		}

		.planned-end-date-assignments {
			margin-left: 10%;
		}

		.assigments-form-details,
		.assigments-form-plan {
			display: grid;
			grid-template-columns: 50% 50%;
			grid-gap: 16px;
			margin-bottom: 20px;
			width: 98%;


			& .form-field {
				margin: 0 !important;
			}
		}



		.alert-content {
			&::before {
				content: none;
			}
		}

		.date-time-split-field {
			.ember-power-select-trigger {
				width: 110px !important;
				padding-right: 0;
			}
		}

		.effort-form-field-wrapper {
			order: 2;
		}

		.edit-assignments-planned-effort {
			width: 55%;
		}

		.date-time-picker-wrapper {
			@include flexbox-simple(start, center);
		}
	}

	.wlm-assignment-issue-agent {
		.tabs__items {
			display: none !important;
		}
	}


	.back-wrapper {
		margin-left: -8px;

		.btn-link {
			@include flexbox-simple(start, center);
		}

		.back-arrow {
			fill: $linkColor;
		}
	}

	.manage-assignment-slider-edit {
		.ember-modal-header {
			padding: 20px !important;
			border-top: none;
		}
	}

	.edit-assignments-description {
		margin-top: 8px;

		.fr-toolbar {
			border-bottom: none !important;
			background-color: transparent !important;

			&::after {
				content: " ";
				width: 95%;
				position: absolute;
				left: 10px;
				top: 38px;
				border-bottom: 1px solid $tableColumnSeparator;
			}
		}

		.fr-wrapper {
			background-color: $white;
			color: $inputColor;
			font-size: $title-small;
			font-family: inherit;
			font-weight: $text-weight-medium;
			padding: 6px 12px;
			line-height: 1.5;
			border-top: none;
			border-radius: 0 !important;
			height: auto !important;

			.fr-element.fr-view {
				margin: 0;
				padding: 0 !important;
			}
		}

		.fr-second-toolbar {
			border-bottom: 1px solid $app-border-primary  !important;
			border-radius: 0 0 4px 4px;
		}
	}



	.edit-assigments-wrapper {
		.ember-modal-body {
			border-top: none;
		}

	}
}

.entity-type-switcher-dropdown {
	.basic-dropdown-item {
		&::after {
			top: 8px;
		}
	}
}
