.filter-by-dropdown {
	svg {
		display: none;
	}
}

.user-options-container {
	display: flex;
	align-items: center;

	.user-options-label {
		padding-left: 5px;
		overflow: hidden;
	}
}

.filter-label {
	padding-left: 16px;
	padding-top: 12px;
	padding-bottom: 12px;
	font-style: normal;
	font-weight: $text-weight-semi-bold;
	font-size: $baseFontSize;
	line-height: 20px;
	color: $primary-text-color-1;

	&::after {
		content: " ";
		margin-top: 15px;
		border-bottom: 1px solid $border-dark-color;
		width: 100%;
		position: absolute;
		display: block;
		left: 0;
	}
}

.search-container {
	padding: 6px 16px;

	.ember-power-select-multiple-options {
		padding: 1px 0 0 8px;
		font-weight: $text-weight-semi-bold;
	}

	.ember-power-select-multiple-option {
		padding: 4px 0 4px 10px !important;
	}

	.ember-power-select-multiple-remove-btn {
		width: 24px !important;
		height: 24px !important;

		svg {
			width: 24px !important;
			height: 24px !important;
		}
	}
}

.dhx_matrix_line {
	width: 90% !important;
}

.radio-buttons-group {
	display: flex;
	width: 360px;
	align-items: center;
	@include flexbox-simple(space-between, center);
	padding: 20px 16px 4px 16px;
}

.people-filter-footer {
	@include flexbox-simple(flex-end, center);
	padding: 0 0 8px 0;
}


.people-filter-label {
	background: $background-color-2;
	border-radius: 18px;
	font-weight: $text-weight-semi-bold;
	font-size: $text-normal;
	line-height: 20px;
	color: $app-grey;
	padding: 4px 8px;
}

.people-filter-reset-label {
	font-style: normal;
	font-weight: $text-weight-semi-bold;
	font-size: $text-normal;
	line-height: 20px;
	color: $linkColor;

}

.users-label {
	padding-left: 12px;
	font-family: $baseFontFamily;
	font-style: $text-normal;
	font-weight: $text-weight-normal;
	font-size: $baseFontSize;
	line-height: 20px;
	overflow: hidden;
	color: $app-grey
}

.divider {
	width: 232px;
	height: 1px;
	background: $modal-footer-border-color;
}

.people-header {
	background: $app-white;
	@include flexbox-simple(space-between, center);



	.wlm-people-filter-trigger {
		z-index: 3;
		position: absolute;
		top: -35px;
		left: 10px;

		&--active {
			margin-right: 0;
			position: relative;
			@include filter-active();
		}
	}

	.wlm-dropdown {
		position: absolute;
		left: 180px !important;
		top: 105px !important;
	}
}

.wlm-people-filter-content {
	top: 0 !important;
}


.filter-view {
	&-container {
		@include flexbox-simple(start, center);
		min-width: 40%;
		flex-grow: 1;

		label {
			cursor: pointer;
			padding: 0;
		}
	}

	&-placeholder {
		padding-top: 4px;
		display: inline-block;
		font-size: $text-normal;
		color: $inputHoverBorder;
	}
}


.people-label {
	display: block;
	align-items: center;
}

.people-filter {
	background: $app-white;
	border: 1px solid $inputDisabledBorder;
	box-sizing: border-box;
	border-radius: 4px;
}


.wlm-people-filter-option {

	&:hover,
	&:focus {
		background: $dropdown-hover;
		outline: none;
	}
}

.wlm-option--active {
	font-weight: $text-weight-semi-bold;
	background-color: $dropdown-option-selected;

	&:hover {
		background-color: $dropdown-option-selected;
	}

	&::before {
		position: absolute;
		top: 0;
		right: 1% !important;
		bottom: 0;
		@include flexbox-simple(center, center);
		flex-direction: column;
	}

}
