.wlm-page-wrapper .page-content {
	min-height: unset !important;

	.manage-assignment-container {
		@include flexbox-simple(center, center);
		width: 100%;
		height: 0;
		padding: 12px;
		cursor: pointer;
	}

	.wlm-gantt-empty-section {
		z-index: 1;
		position: absolute;
		top: 40px;
		left: 35%;
		@include flexbox-simple(space-between, center);
		flex-direction: column;
		height: 130px;

		& .decorative-image {
			width: 77px;
			height: 77px;
		}
	}

	.scheduler-empty-state {
		font-size: $title-small;
	}

	.wlm-gantt-empty-message {
		font-weight: $text-weight-medium;
		font-size: $title-small;
	}

	.wlm-gantt-plan-assignments {
		font-weight: $text-weight-semi-bold;
		font-size: $title-small;
		color: $linkColor  !important;
		cursor: pointer;
		width: 108px;
		border: none;
		@include flexbox-simple(space-between, center);

		&:hover,
		&:focus {
			border: none;
			outline: none;
		}
	}

	.wlm-user-container {
		display: flex;
		align-items: center;
		position: absolute;
	}

	.user_image {
		width: 28px;
		height: 28px;
	}

	.wlm-user-name {
		padding-left: 6px;
		width: 172px;
		text-align: left;
		@include line-clamp($lines: 1);
	}

	.wlm-user-name-indicator {
		padding-left: 5px;
		color: $toggleSlider;
	}

	.wlm-timeline-border {
		position: absolute;
		height: 40px;
		border-left: 1px solid $border-light-color;
		top: -80px;
		margin-left: 8px;
	}

	.wlm-people-filter-content {
		width: 400px !important;
	}

	.shimmer-loader {
		.summary-header-loader {
			margin-left: 38%;
		}

		.workload-shimmer-container {
			overflow-x: hidden;
		}

		.wlm-calendar-timeline-date {
			@include flexbox-simple(center, center);
			margin-left: 24%;
			margin-top: 8px;
			height: 32px;
			padding-bottom: 10px;
		}

		.wlm-calendar-date {
			@include flexbox-simple(center, center);
			margin-left: 12%;
			margin-bottom: 5px;
			height: 32px;
			width: 98%;

			.group {
				justify-content: space-between;
				margin-left: 1px;
				margin-right: 1px;
			}
		}

		.wlm-calender-data-row {
			@include flexbox-simple(flex-start, center);
			height: 100px;
			position: relative;
			border-bottom: 1px solid $border-light-color;
		}

		.wlm-calendar-grid {
			position: relative;

			&::before {
				position: absolute;
				content: " ";
				border-left: 1px solid $border-light-color;
				height: 142px;
				top: -60px;
				left: -8px;
			}
		}

		.wlm-calendar-grid-top {
			border-top: 1px solid $border-light-color;
		}

		.wlm-calendar-date-top {
			&::before {
				border-top: 1px solid $border-light-color;
				width: calc(100% + 15px);
			}
		}
	}

	.dhx_cal_event_line {
		padding: 0;
	}

	.wlm-my-workload {
		font-weight: $text-weight-semi-bold;
	}

	.wlm-gantt-key {
		font-weight: $text-weight-semi-bold;
		max-width: 88px;
		text-align: center;
		padding: 0 6px;
		@include line-clamp();
	}

	.wlm_gantt_label {
		@include flexbox-simple(start, center);
		width: 100%;
		padding: 0 10px;
	}

	.wlm-gantt-title {
		max-width: 116px;
		@include flexbox-simple(start, center);
	}

	.gantt-action-disabled {
		cursor: no-drop !important;
	}

	.gantt-icon {
		@include flexbox-simple(start, center);
	}

	.wlm-gantt-info {
		position: absolute;
	}

	.gantt-info-section {
		min-width: 296px
	}

	.gantt-info-status {
		font-weight: $text-weight-semi-bold;
		padding-left: 36px;
		margin-top: 11px;
		color: $inputColor;
		font-family: $baseFontFamily  !important;
		font-size: $baseFontSize  !important;
		letter-spacing: 0;
		text-transform: uppercase;

		&.open::before {
			@extend .gantt-status-highlighter;
			background: $app-sidebar-bg;
			border: 1px solid $app-grey-bg-2;
		}

		&.delayed::before {
			@extend .gantt-status-highlighter;
			background: $alertWarningBackground;
			border: 1px solid $alertWarningBorder;
		}

		&.in-progress::before {
			@extend .gantt-status-highlighter;
			background: $alertInfoBackground;
			border: 1px solid $border-light-blue;
		}

		&.done::before {
			@extend .gantt-status-highlighter;
			background: $app-jade-light;
			border: 1px solid $hummingBirdGreenBorder;
		}
	}

	.gantt-info-title {
		@include flexbox-simple(start, baseline);
		padding: 6px;
		cursor: pointer;
		color: $linkColor;
		margin-left: 10px;
		flex: 1;
	}

	.gantt-info-date {
		padding-left: 16px;
		color: $app-form-label-color;
		margin-bottom: 20px;
		font-size: $baseFontSize;
	}

	.wlm-gantt-bar-info {
		cursor: pointer;
	}

	.wlm-gantt-bar-info>svg {
		display: block;
	}

	.gantt-section-add-effort {
		padding: 16px;
		text-transform: capitalize;
	}

	.gantt-effort-hours {
		padding-left: 2px;
	}

	.gantt-info-heading {
		font-weight: $text-weight-bold;
		color: $linkColor;
		cursor: pointer;
		font-size: $baseFontSize;
	}

	.subject_label {
		padding-left: 6px;
		@include line-clamp($lines: 1);
		font-weight: $text-weight-semi-bold;
	}

	.ember-basic-dropdown-content {
		z-index: 9;
	}

	.dhx_cal_qi_controls {
		display: none;
	}

	.dhx_cal_quick_info {
		border: none;
		padding: 0;
		width: 375px;
		margin-top: 15px;
		background: $white;
		box-shadow: 0 2px 12px rgba(0, 0, 0, 0.14);
	}

	.dhx_cal_qi_tdate {
		display: none;
	}

	.dhx_cal_qi_title {
		border-radius: 4px 4px 0 0;
		background: $white;
		padding: 5px 0 0 5px;
	}


	.add-planned-date {
		color: $linkColor;
		font-weight: $text-weight-medium;
		cursor: pointer;
		text-transform: capitalize;
	}

	.add-planned-effort {
		color: $linkColor;
		font-weight: $text-weight-semi-bold;
		cursor: pointer;
		text-transform: uppercase;
	}

	.dhx_cal_qi_content {
		border: none;
		width: initial;
		border-radius: 0 0 4px 4px;
		font-family: $baseFontFamily  !important;
		font-size: $baseFontSize  !important;
		padding: 4px 8px;
	}

	.gantt-resizer {
		content: " ";
		height: 14px;
		width: 2px;
		position: absolute;
		background: $white;
		left: -5px;
		top: 7px;
		border-radius: 2px;
	}

	.gantt-status-highlighter {
		content: " ";
		width: 10px;
		height: 10px;
		position: absolute;
		left: 20px;
		top: 19px;
		border-radius: 10px;
	}

	.gantt-status {
		font-size: $text-normal;
		@include flexbox-simple(center, start);
		flex-direction: column;
		height: 28px !important;
		color: $app-icon-color;
		margin-top: 15px;
		border: none;
		border-radius: 3px;
		margin-left: 2px !important;
		cursor: default;

		&:last-of-type~.dhx_timeline_data_row {
			border-bottom: 1px solid $border-light-color;
		}
	}

	.gantt-status-done {
		background: $app-jade-light;
		border: 1px solid $hummingBirdGreenBorder;

		& .dhx_event_resize {
			border-left: 8px solid $hummingBirdGreenBorder;

			&::before {
				@extend .gantt-resizer;
			}
		}

		& .dhx_event_resize_end {
			border-left: 8px solid $hummingBirdGreenBorder;
		}
	}

	.gantt-status-in-progress {
		background: $alertInfoBackground;
		border: 1px solid $border-light-blue;

		& .dhx_event_resize {
			border-left: 8px solid $border-light-blue;

			&::before {
				@extend .gantt-resizer;
			}
		}

		& .dhx_event_resize_end {
			border-left: 8px solid $border-light-blue;
		}
	}

	.gantt-status-open {
		background: $app-sidebar-bg;
		border: 1px solid $app-grey-bg-2;

		& .dhx_event_resize {
			border-left: 8px solid $app-grey-bg-2;

			&::before {
				@extend .gantt-resizer;
			}
		}

		& .dhx_event_resize_end {
			border-left: 8px solid $app-grey-bg-2;
		}
	}

	.gantt-status-delayed {
		background: $alertWarningBackground;
		border: 1px solid $alertWarningBorder;

		& .dhx_event_resize {
			border-left: 8px solid $alertWarningColor;

			&::before {
				@extend .gantt-resizer;
			}
		}

		& .dhx_event_resize_end {
			border-left: 8px solid $alertWarningColor;
		}
	}

	.is-start {
		border-width: 1px;
		margin-left: 1px;
	}

	.add-more-event {
		@include flexbox-simple(start, center);
		position: absolute;
		bottom: -5px;
		left: 40%;
	}


	.wlm-scheduler-wrapper {
		height: calc(100vh - 125px) !important;
	}
}

.workload_container {
	.ember-pagination-new {
		background: $white;
	}
}

.workload_container.people-empty #scheduler_container {
	height: 72px;
}

.people-empty-container {
	height: calc(100vh - 257px);
	font-weight: $text-weight-medium;
	font-size: $text-normal;
	@include flexbox-simple(center, center);
	flex-direction: column;

	.people-empty-icon {
		width: 80px;
		margin-bottom: 16px;
	}
}

#scheduler_container {
	height: calc(100vh - 185px);
	width: 100%;

	.dhx_cal_navline {
		height: 32px !important;
		border-bottom: 1px solid $border-light-color;
	}

	.dhx_cal_date {
		font-weight: $text-weight-semi-bold;
		font-size: $text-normal;
		color: $app-grey;
		height: 32px;
		line-height: 1px;
	}

	.dhx_cal_timeline_date {
		margin: 5px 0 0 50%;
		font-weight: $text-weight-semi-bold;
		text-transform: uppercase;
	}

	.dhx_cal_header {
		height: 32px !important;
		left: 0 !important;
		border-bottom: 1px solid $border-light-color;
		border-top: none;
	}

	.dhx_timeline_label_row.dhx_row_item {
		left: 1px !important;
	}

	.dhx_matrix_line {
		width: 90% !important;

		.wlm-cell-template:hover {
			box-shadow: 0px 4px 6px $scorecardHoverShadow;
		}
	}

	.dhx_cal_data {
		margin-top: 10px;
		left: -3px !important;
		font-family: $baseFontFamily  !important;
	}

	.dhx_timeline_label_row.dhx_row_item .dhx_matrix_scell.item:not(:nth-of-type(1)) {
		display: none !important;
	}

	.dhx_timeline_label_row {
		.folder .dhx_scell_name {
			z-index: 9;
			text-transform: capitalize;
			font-weight: $text-weight-normal;
			font-size: $baseFontSize;
			font-family: $baseFontFamily  !important;
			@include flexbox-simple(space-between, center);
		}
	}

	.dhx_timeline_data_row.dhx_data_table.folder {
		border-color: $border-light-color;
		border-style: solid;
		border-width: 0 1px 0 1px;
		left: 1px !important;
	}

	.dhx_scale_bar {
		height: 42px !important;
		border-right: 1px solid $border-light-color;
		padding-top: 6px;
		border-left: none;
		margin-left: -1px;
	}

	.dhx_timeline_data_cell {
		border: none;
	}

	.dhx_matrix_cell.dhx_timeline_data_cell {
		border-bottom: 1px solid $border-light-color;
	}

	.dhx_data_table.folder .dhx_timeline_data_cell {
		border-right: 1px solid $border-light-color;
	}

	.wlm-scale-header-date {
		@include flexbox-simple(space-between, center);
		padding: 0 8px 0 8px;
		text-transform: uppercase;
		font-weight: $text-weight-semi-bold;
		font-size: $text-normal;
		color: $app-grey;

		.wlm-header-date {
			color: $app-form-label-color;
		}
	}

	.wlm-cell-template-wrapper {
		width: 100%;
		height: 100%;
		padding: 16px 8px;
	}

	.wlm-cell-background {
		width: 100%;
		height: 100%;
		background-color: $wlm-current-date-background;
	}

	.dhx_matrix_scell.folder {
		border-bottom: 1px solid $border-light-color;
	}

	.dhx_data_table.folder .dhx_matrix_cell,
	.dhx_matrix_scell.folder {
		background: $white;
	}

	.dhx_matrix_line .shimmer-loader {
		position: absolute;
		top: 0;
		padding-left: 5%;

		& .second-row {
			padding-left: 15%;
		}

		& .third-row {
			padding-left: 25%;
		}
	}

	.dhx_matrix_scell.item {
		margin-left: 1px;
		border-right: 1px solid $border-light-color;
		border-bottom: 1px solid $border-light-color;
		min-height: 110px;

		.wlm-item-category {
			position: relative;
			top: -95px;
			@include flexbox-simple(center, center);
		}



		.dhx_scell_name {
			font-family: $baseFontFamily;
			font-size: $baseFontSize;
			padding-left: 0;
			cursor: pointer;
			font-weight: $text-weight-normal;
		}
	}

	.dhx_scell_level1 {
		padding-left: 0;
	}

	.wlm-cell-template {
		display: flex;
		height: 100%;
		border-radius: 4px;
	}

	.wlm-cell-allocation-under {
		border: 1px solid $alertPartialBorder;
		background: $app-casablanca-light;
		color: $primary-text-color-1;

		&:hover {
			border: 1px solid $svg-orange;
		}
	}

	.wlm-cell-allocation-over {
		border: 1px solid $alertWarningBorder;
		background: $alertWarningBackground;
		color: $alertWarningColor;

		&:hover {
			border: 1px solid $filter-active-indicator;
		}
	}

	.wlm-cell-allocation-optimally {
		border: 1px solid $optimally-allocated-border;
		background: $optimally-allocated-background;
		color: $primary-text-color-1;

		&:hover {
			border: 1px solid $app-green;
		}
	}

	.wlm-cell-allocation-not {
		border: 1px dashed $tableRowHighlightMarker;
		background: $app-white;
		color: $primary-text-color-1;

		&:hover {
			border: 1px dashed $secondary-text-color-2;
		}
	}

	.wlm-cell-allocation-value {
		width: 100%;
		padding: 14px 8px;
		font-size: $title-small;
		font-weight: $text-weight-semi-bold;
		@include flexbox;
		@include align-items(center);
	}

	.wlm-current-date-header {
		background-color: $wlm-current-date-background;
		height: 100%;
		margin-top: -14px;
		border: none;

		.wlm-header-day {
			color: $app-primary;
			font-weight: $text-weight-semi-bold;
		}

		.wlm-header-date {
			background: $inputFocusBorder;
			border-radius: 50%;
			width: 24px;
			height: 24px;
			line-height: 2;
			margin-top: 3px;
			color: $white;
		}
	}



	.dhx_scell_expand {
		position: relative;
		margin: 10px;

		&::after {
			content: "";
			background: none;
		}
	}

	.wlm-dhx-scell-icon {
		display: block;
		content: " ";
		width: 16px;
		height: 16px;
		background: url("/a/assets/inline-svg/app-common/chevron-right.svg") no-repeat center;
		border-radius: 8px;
		padding-left: 1px;
		box-sizing: border-box;
	}

	.dhx_timeline_scale_header {
		border-right: 1px solid $border-light-color;
		z-index: 3;
		padding: 10px;
		margin-left: -1px;
		top: 1px !important;
		left: 1px !important;
		height: 64px !important;
		justify-content: start !important;
		font-family: $baseFontFamily  !important;
	}



	.wlm-scale-header-label {
		@include flexbox-simple(flex-start, flex-start);
		flex-direction: column;

		.wlm-scale-count-header {
			font-size: $title-small;
			font-weight: $text-weight-normal;
		}

		.wlm-scale-header-list-count {
			font-weight: $text-weight-semi-bold;
			font-size: $title-small;
		}

		.wlm-header-label {
			color: $linkColor;
			font-size: $text-normal;
			padding: 3px 4px;
			text-transform: lowercase;

			&::first-letter {
				text-transform: capitalize;
			}

			&:hover {
				cursor: pointer;
			}
		}

		.wlm-header-reset {
			display: inline-flex;
			align-items: center;
			padding: 0 6px;
			font-weight: $text-weight-semi-bold;
			border: 1px solid transparent;
			background: $app-sidebar-bg;
			font-size: $text-normal;
			border-radius: 36px;
			text-transform: lowercase;
		}
	}



	.wlm-scale-header-wrapper {
		@include flexbox-simple(space-between, center);
	}

	.dhx_matrix_scell.folder.closed .dhx_scell_expand {
		@extend .wlm-dhx-scell-icon;
	}

	.dhx_matrix_scell.folder.opened .dhx_scell_expand {
		@extend .wlm-dhx-scell-icon;
		top: 2px;
		transform: rotate(-90deg);
	}

}

.wlm-gantt-left-pane {
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
}

.wlm-left-pane-metric {
	line-height: 20px;
	padding: 16px;
	border-bottom: 1px solid $border-light-color;
	@include flexbox-simple(center, center);
}

.wlm-left-pane-metric .gantt-count-container {
	@include flexbox-simple(center, center);
	margin-right: 4px;
}

.wlm-left-pane-metric .gantt-count {
	height: 17px;
	font-size: $text-normal;
	font-weight: $text-weight-semi-bold;
	color: $white;
	background: $linkColor;
	padding: 2px 8px;
	margin-right: 4px;
	border-radius: 8px;
	@include flexbox-simple(center, center);
}

.wlm-left-pane-metric .gantt-count-title {
	font-size: $baseFontSize  !important;
}

.wlm-left-pane-assignment-action {
	line-height: 20px;
	padding-top: 18px;
	@include flexbox-simple(center, start);

	.btn {

		&:hover,
		&:active,
		&:focus {
			border: none;
			outline: none;
		}
	}
}

.wlm-left-pane-assignment-action .manage-assignment-label {
	color: $linkColor;
	font-weight: $text-weight-semi-bold;
	position: relative;
	font-size: $title-small;
	text-transform: capitalize;
}

.wlm-left-pane-assignment-action .manage-assignment-edit {
	margin-right: 5px;
}

.wlm-left-pane-assignment-action .manage-assignment-edit path {
	fill: $linkColor;
}
